<template>
  <div style="min-height: 2000px;">
    <!--<div>{{dragAndResizeArr}}</div>-->

    <div style="position: relative;">
      <div v-for="item in dragAndResizeArr" :style="item.objStyle">
        <component :is="item.name" :key="item.id" :item="item"></component>
      </div>
    </div>
  </div>
</template>

<script>
  import componentAll from './importComponents'
  import store from '@/store'

  export default {
    name: "dragAndResizePreview",
    components: {
      ...componentAll
    },
    computed: {
      dragAndResizeArr() {
        return store.getters.dragAndResizeArr
      }
    }
  }
</script>

<style scoped>

</style>



